import { Injectable, Pipe, PipeTransform } from '@angular/core';
import { IConvocatory, Idocument } from '../interfaces/interfaces';

@Pipe({
  name: 'docsFilter'
})
@Injectable()
export class DocsFilterPipe implements PipeTransform {

  transform(items: IConvocatory[], param: string): any[] {
    if (param === undefined) { return items; }
    if ( param.length < 4 ) { return items; }

    param = param.toLowerCase();

    if (items) {
      return items.filter((item, index) => {
        const find = item.titulo.toLowerCase().indexOf(param);
        return find === -1 ? false : true;
      });
    }

  }

}
