import { Pipe, PipeTransform } from '@angular/core';
import { IConvocatory } from '../interfaces/interfaces';

@Pipe({
  name: 'convFilter'
})
export class ConvFilterPipe implements PipeTransform {

  validConv: IConvocatory[] = [] ;

  transform(items: IConvocatory[], estado: string): any {

    if (estado === 'all') {
      return items;
    }

    if (items != []) {
      return items.filter((item, index) => item.estado === estado);
    }


  }

}
